.container {
  width: 607px;
  height: 829px;
  margin: auto;
  margin-top: 75px;
  margin-bottom: 75px;
  border-radius: 30px;
  background-image: linear-gradient(180deg, #286db2 0%, #120a3c 100%);
}
.top-bar {
  display: flex;
  justify-content: center;
  gap: 16px;
  padding-top: 50px;
}
.top-bar input {
  display: flex;
  width: 362px;
  height: 70px;
  background: #ebfffc;
  border: none;
  outline: none;
  border-radius: 40px;
  padding-left: 40px;
  color: #626262;
  font-size: 20px;
  font-weight: 400;
}
.search-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  background: #ebfffc;
  border-radius: 40px;
  cursor: pointer;
}

.weather-image {
  margin-top: 18px;
  display: flex;
  justify-content: center;
}

.weather-temp {
  display: flex;
  justify-content: center;
  color: white;
  font-size: 110px;
  font-weight: 400;
}

.weather-location {
  display: flex;
  justify-content: center;
  color: white;
  font-size: 50px;
  font-weight: 400;
}

.data-container {
  margin-top: 100px;
  color: white;
  display: flex;
  justify-content: center;
}

.element {
  margin: auto;
  display: flex;
  align-items: center;
  gap: 15px;
}

.data {
  font-size: 34px;
  font-weight: 400;
}

.text {
  font-size: 20px;
  font-weight: 400;
}

.icon {
  margin-top: 10px;
}
